* {
  box-sizing: border-box;
}
#container {
  width: 100%;
  min-width: 980px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  /* background: green; */
}

.row {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
}

.image {
  width: 100%;
  height: 100%;
}

.row > div {
  width: 25%;
  height: 17vw;
  min-width: 327px;
  min-height: 221.719px;
}

.logo {
  position: fixed;
  width: 200px;
  bottom: 10px;
  left: 0px;
  opacity: 0.8;
}
